body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.parent {
  /* display: flex; */
  /* justify-content: center; */
  margin: auto;
}

.inputText {
  border: 5px solid #0d8bf2; 
  box-shadow: 
    inset 0 0 8px  rgba(0,0,0,0.1),
          0 0 16px rgba(0,0,0,0.1); 
  padding: 15px;
  background: rgba(255,255,255,0.3);
  margin: 0 10px 10px 0;
  width: 60%;
  border-radius: 5rem;
  font-size: 16px;
  font-family: Futura, 'Trebuchet MS', Arial, sans-serif;
  text-align: center;
}

.inputText::-webkit-input-placeholder {
  color: #888888;
}

.inputText::-ms-input-placeholder {
  color: #888888;
}

.inputText::placeholder {
  color: #888888;
}

.inputText:focus {
  border: 5px solid #0b7dda;
  outline: none;
}

.autosuggestion {
  position: relative;
  /* z-index: -1; */
  /* margin: auto; */
  text-align: center;
}

.suggestions {
  position: absolute;
  top: 75px;
  width: 60%;
  z-index: 1;
  margin-left: 10px;
}

.suggestions div{
  list-style-type: none;
  border-radius: 5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  /* align-items: center; */  
}

.showlist {
  /* visibility: visible; */
  display: block;
}

.suggestions div span {
  padding: 10px;
  /* width: 500px; */
  border-bottom: 0.5px solid #888;  
  background-color: rgba(255, 255, 255, 0.8);
}
.card {
	margin: 10px;
	border: 1px solid #eee;
	/* box-shadow: 1px 5px 3px #bfbfbf; */
	box-shadow: 1px 5px 3px #bfbfbf;
	width: 200px;
	padding: 20px;
	background-color: #eee; /* #0d8bf2; */
	color: #000000;
	font-family: cursive;
	text-align: center;
}


.w3-black {
	/* color: #fff; */
	background-color: #eee;
	padding: 5rem;
	margin: 1rem;
}

.w3-card-4 {
	box-shadow:0 4px 10px 0 rgba(0, 0, 0, 0.5),0 4px 20px 0 rgba(0, 0, 0, 0.19);
	padding: 3rem;
	margin: 1rem;
	text-align: center;
}

.w3-padding-64 {
    padding-top: 64px;
    padding-bottom: 64px;
}

.w3-hover-shadow:hover{
	box-shadow:0 4px 10px 0 rgba(0,0,0,0.2),0 4px 20px 0 rgba(0,0,0,0.19)
}
.App {
  /* text-align: center; */
  margin: 1rem;
}

.center {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
  background: #2196F3;
}

.cardList {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: wrap;
          flex-flow: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.butn {
  border-radius: 28px;
  font-family: Futura, 'Trebuchet MS', Arial, sans-serif;
  color: #000000;
  font-size: 16px;
  padding: 15px 20px 15px 20px;
  text-decoration: none;
  border: 1px solid #0d8bf2 !important;
  margin: 3px;
  width: 10rem;
  /* position: absolute; */
}

.butn:hover {
  background: #bfbfbf;
  text-decoration: none;
  /* border: 1px solid #0b7dda !important; */
  border: 1px solid #eee !important;
}

