.parent {
  /* display: flex; */
  /* justify-content: center; */
  margin: auto;
}

.inputText {
  border: 5px solid #0d8bf2; 
  -webkit-box-shadow: 
    inset 0 0 8px  rgba(0,0,0,0.1),
          0 0 16px rgba(0,0,0,0.1); 
  -moz-box-shadow: 
    inset 0 0 8px  rgba(0,0,0,0.1),
          0 0 16px rgba(0,0,0,0.1); 
  box-shadow: 
    inset 0 0 8px  rgba(0,0,0,0.1),
          0 0 16px rgba(0,0,0,0.1); 
  padding: 15px;
  background: rgba(255,255,255,0.3);
  margin: 0 10px 10px 0;
  width: 60%;
  border-radius: 5rem;
  font-size: 16px;
  font-family: Futura, 'Trebuchet MS', Arial, sans-serif;
  text-align: center;
}

.inputText::placeholder {
  color: #888888;
}

.inputText:focus {
  border: 5px solid #0b7dda;
  outline: none;
}

.autosuggestion {
  position: relative;
  /* z-index: -1; */
  /* margin: auto; */
  text-align: center;
}

.suggestions {
  position: absolute;
  top: 75px;
  width: 60%;
  z-index: 1;
  margin-left: 10px;
}

.suggestions div{
  list-style-type: none;
  border-radius: 5rem;
  display: flex;
  flex-direction: column;
  /* align-items: center; */  
}

.showlist {
  /* visibility: visible; */
  display: block;
}

.suggestions div span {
  padding: 10px;
  /* width: 500px; */
  border-bottom: 0.5px solid #888;  
  background-color: rgba(255, 255, 255, 0.8);
}